.main-header {
  min-height: 85px;
  padding: 12px 21px 0 18px;
  position: relative;
}

.main-header::before {
  content: "";
  position: absolute;
  top: 47%;
  width: 375px;
  height: 1px;
  border: 1px solid #6F7682;
}

.main-nav {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.main-nav__logo {
  margin-right: 52px;
  cursor: pointer;
}

.main-nav__list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.main-nav__link {
  color: #4798DE;
  text-decoration: none;
  cursor: pointer;
}

.main-nav__catalog {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  margin-top: 25px;
}

.main-nav__name {
  font-size: 14px;
  line-height: 18px;
  color: #4798DE;
  margin-right: 30px;
  margin-left: 10px;
}

.main-nav__input {
  display: flex;
  align-items: center;
  text-align: center;
  width: 140px;
  height: 25px;
  background: #FFFFFF;
  border: 1px solid #979595;
  font-weight: 400;
  font-size: 10px;
  line-height: 13px;
  color: #6F7682;
  cursor: pointer;
}

.main-nav__button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 46px;
  height: 25px;
  background: #4798DE;
  border: 0;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
}

.page__body {
  background-color: #FFFFFF;
  font-family: "PT Sans", "Roboto", sans-serif;
  margin: 0;
}

.main-container {
  padding-top: 15px;
}

.presentation {
  position: relative;
}

.presentation__photo::after {
  content: "";
  position: absolute;
  top: 15%;
  right: 5%;
  width: 142px;
  height: 156px;
  background: rgba(255, 255, 255, 0.75);
}

.presentation__button {
  background: rgba(241, 242, 246, 0.3);
  border-radius: 2px;
  border: none;
  cursor: pointer;
}
.presentation__button--back {
  position: absolute;
  top: 35%;
  left: 0;
}
.presentation__button--ahead {
  position: absolute;
  top: 35%;
  right: 94%;
}

.presentation-info {
  position: absolute;
  top: 16%;
  left: 223px;
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.presentation-info__detail {
  max-width: 145px;
  font-size: 14px;
  line-height: 18px;
  color: #6F7682;
}
.presentation-info__detail--color {
  color: #6CDB7E;
}

.presentation-info__button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100px;
  height: 30px;
  background: #4798DE;
  border: 1px solid #4798DE;
  border-radius: 5px;
  font-size: 13px;
  line-height: 17px;
  color: #FFFFFF;
  cursor: pointer;
}

@font-face {
  font-family: "PT Sans";
  font-weight: 400;
  font-style: normal;
  src: url("/src/fonts/ptsans.woff2") format(woff2), url("/src/fonts/ptsans.woff") format(woff);
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-weight: 700;
  font-style: normal;
  src: url("/src/fonts/ptsansbold.woff") format(woff), url("/src/fonts/ptsansbold.woff2") format(woff2);
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}